const SEND_MESSAGE = "SEND-MESSAGE";

let initialState = {
    messagesData: [
        {id: 1, message: "hello",},
    ],
    dialogsData: [
        {id: 1, name: "Vika",},
        {id: 2, name: "Olga",},
        {id: 3, name: "Anna",},
        {id: 4, name: "Oleg",},
        {id: 5, name: "Pavel",},
        {id: 6, name: "Denis",},
        {id: 7, name: "Sveta",},
    ],
}

const messagesReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let newText = action.newMessageText;
            return {
                ...state,
                messagesData: [...state.messagesData, {id: 2, message: newText}],
            };

        default:
            return state;
    }
}

export const addMessageActionCreator = (newMessageText) => ({type: SEND_MESSAGE, newMessageText});

export default messagesReducer;