import s from './Header.module.css';
import {NavLink} from "react-router-dom";

const Header = (props) => {

    return <header className={s.header}>
        <img src="https://free-png.ru/wp-content/uploads/2020/10/07-24926446.png"
             alt=""/>
        <i>cherry</i>
        <div className={s.loginBlock}>
            { props.isAuth
                ? <div>{props.login} - <button onClick={props.logout}>Log out</button> </div>
                : <NavLink to={'/login'}>Login</NavLink> }
        </div>
        <div className={s.leftMenu}>
            <form className={s.searchForm} action="/">
                <input type="search" className={s.search} placeholder="Search for anything"/>
                <button className={s.searchButton}></button>
            </form>
            <a href="#" className={s.link + " " + s.linkPerson}><span>7</span></a>
            <a href="#" className={s.link + " " + s.linkMessages}><span>3</span></a>
            <a href="#" className={s.link + " " + s.linkNotification}><span>5</span></a>
            <a href="#" className={s.linkProfile}>
                <img src="https://vraki.net/sites/default/files/inline/images/20_32.jpg" alt=""/>
            </a>
        </div>
    </header>;
}

export default Header;