import s from './Navbar.module.css';
import {NavLink} from "react-router-dom";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faUser , faEnvelope, faNewspaper, faMusic, faCogs, faUsers} from '@fortawesome/free-solid-svg-icons'


const Navbar = () => {
    const userIcon = <FontAwesomeIcon icon={faUser}/>
    const messageIcon = <FontAwesomeIcon icon={faEnvelope}/>
    const newsIcon = <FontAwesomeIcon icon={faNewspaper}/>
    const musicIcon = <FontAwesomeIcon icon={faMusic}/>
    const settingsIcon = <FontAwesomeIcon icon={faCogs}/>
    const usersIcon = <FontAwesomeIcon icon={faUsers}/>
    return (
        <nav className={s.nav}>
            <NavLink className={s.item} to="/profile">
                <span className={s.icon}> {userIcon}</span>
                <span>Profile</span>
            </NavLink>
            <NavLink className={s.item} to="/dialogs">
                <span className={s.icon}> {messageIcon}</span>
                <span>Messages</span>
            </NavLink>
            <NavLink className={s.item} to="/news">
                <span className={s.icon}> {newsIcon}</span>
                <span>News</span>
            </NavLink>
            <NavLink className={s.item} to="/music">
                <span className={s.icon}> {musicIcon}</span>
                <span>Music</span>
            </NavLink>
            <NavLink className={s.item} to="/settings">
                <span className={s.icon}> {settingsIcon}</span>
                <span>Settings</span>
            </NavLink>
            <NavLink className={s.item} to="/users">
                <span className={s.icon}> {usersIcon}</span>
                <span>Users</span>
            </NavLink>
        </nav>
    )
}
export default Navbar;